import React from 'react'

const Cell = (props) => {
    let cellClass = getCellClass(props)
    const cellContainsKnight =
        props.knightPos.x===props.x && props.knightPos.y===props.y

    return (
        <div
            className={cellClass}
            // conditionally set onClick listener if there is no knight on the board
            onClick={(knightPos.x === undefined) ? ()=>{props.setKnight(props.x, props.y)} : undefined}
        >
            {cellContainsKnight ? "🐴" : props.turnsAvailable}
        </div>
    )
}

export default Cell

const getCellClass = (props) => {
    let cellClass = "board__row__cell"
    

    if ( (props.x + props.y) % 2 )
        cellClass += " board__row__cell--dark"
    if (cellContainsKnight)
        cellClass += " board__row__cell--knight"
    
    return cellClass
}