import React from 'react'
import Cell from './Cell'

const Row = (props) => (
    <div className="board__row">
        {
            props.row.map((cell, cellIndex) => 
                <Cell 
                    x={props.x}
                    y={cellIndex}
                    turnsAvailable={cell.turnsAvailable}
                    setKnight={props.setKnight}
                    knightPos={props.knightPos}
                    key={cellIndex} />
            )
        }
    </div>
)

export default Row